<template>
  <div class="Cost">
    <H1>成 本</H1>
    <el-button style="float:right;margin: 10px;" type="primary" size="small" @click="exportExcel">导 出</el-button>
    <el-table
      ref="CostTable"
      :data="costList"
      style="width: 100%">
      <el-table-column
        type="index"
        width="50">
      </el-table-column>
      <el-table-column
        property="F1"
        label="类别"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F2"
        label="含税金额"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F3"
        label="日期"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F4"
        label="单据编号"
        width="100"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F5"
        label="购货单位"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F6"
        label="产品长代码"
        width="200"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F7"
        label="产品名称"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F8"
        label="规格型号"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F9"
        label="单位"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F10"
        label="实发数量"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F11"
        label="销售单价"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F12"
        label="销售金额"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F13"
        label="单位成本"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F14"
        label="成本"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F15"
        label="项目编号"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F16"
        label="订单单号"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F17"
        label="源单单号"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F18"
        label="合同单号"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F19"
        label="部门"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F20"
        label="业务员"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F21"
        label="源单类型"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import $ from 'jquery'
export default {
  name: 'Cost',
  data () {
    return {
      costList: []
    }
  },
  computed: {
    ...mapState({
      curContractNo: state => state.curContractNo
    })
  },
  created () {
    this.getCostList()
  },
  methods: {
    getCostList () {
      var tmpData = '<?xml version="1.0" encoding="utf-8"?>'
      tmpData += '<soap:Envelope xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:xsd="http://www.w3.org/2001/XMLSchema"> '
      tmpData += '<soap:Body> '
      tmpData += '<JA_LIST xmlns="http://tempuri.org/">'
      tmpData += "<FSQL><![CDATA[select * from zz_cost where f15='" + this.curContractNo + "']]></FSQL>"
      tmpData += '</JA_LIST>'
      tmpData += '</soap:Body>'
      tmpData += '</soap:Envelope>'

      this.Http.post('JA_LIST', tmpData
      ).then(res => {
        let xml = res.data
        let parser = new DOMParser()
        let xmlDoc = parser.parseFromString(xml, 'text/xml')
        // 提取数据
        let Result = xmlDoc.getElementsByTagName('JA_LISTResponse')[0].getElementsByTagName('JA_LISTResult')[0]
        let HtmlStr = $(Result).html()
        let Info = (JSON.parse(HtmlStr))
        console.log(Info)
        this.costList = Info
      }).catch((error) => {
        console.log(error)
      })
    },
    // 导出
    exportExcel () {
      require.ensure([], () => {
        const { exportJsonToExcel } = require('../vendor/Export2Excel.js')
        const tHeader = ['类别', '含税金额', '日期', '单据编号', '购货单位', '产品长代码', '产品名称', '规格型号', '单位', '实发数量', '销售单价', '销售金额', '单位成本', '成本', '项目编号', '订单单号', '源单单号', '合同单号', '部门', '业务员', '源单类型']
        const filterVal = ['F1', 'F2', 'F3', 'F4', 'F5', 'F6', 'F7', 'F8', 'F9', 'F10', 'F11', 'F12', 'F13', 'F14', 'F15', 'F16', 'F17', 'F18', 'F19', 'F20', 'F21', 'F22', 'F23']
        const data = this.formatJson(filterVal, this.CostList)
        exportJsonToExcel(tHeader, data, '成本')
      })
    },
    formatJson (filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]))
    }
  }
}
</script>

<style lang="less" scoped>
</style>



