<template>
  <div class="Outlay">
    <H1>费 用</H1>
    <!-- <el-button style="float:left;margin: 10px;" type="info" size="small" icon="el-icon-arrow-left" @click="back">返 回</el-button> -->
    <el-button style="float:right;margin: 10px;" type="primary" size="small" @click="exportExcel">导 出</el-button>
    <el-table
      ref="OutlayTable"
      :data="outlayList"
      style="width: 100%">
      <el-table-column
        type="index"
        width="50">
      </el-table-column>
      <el-table-column
        property="F1"
        label="单据编号"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F2"
        label="申请日期"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F3"
        label="事由"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F4"
        label="申请人"
        width="100"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F5"
        label="申请部门"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F6"
        label="收款单位"
        width="200"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F7"
        label="发生日期"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F8"
        label="费用项目"
        width="150"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F9"
        label="申请费用金额"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F10"
        label="核算项目"
        width="150"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F11"
        label="项目编号"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F12"
        label="摘要"
        width="150"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        property="F13"
        label="备注"
        width="150"
        show-overflow-tooltip>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import $ from 'jquery'
export default {
  name: 'Outlay',
  data () {
    return {
      outlayList: [
        {
          '单据编号': 'BH123',
          '申请日期': '2019-08-30',
          '事由': '事由',
          '申请人': '张三',
          '申请部门': '人事部',
          '收款单位': '上海旺策尔有限公司',
          '发生日期': '2019-08-30',
          '费用项目': '30000',
          '申请费用金额': '30000',
          '核算项目': 'ZT',
          '项目编号': 'BH123456'
        }
      ]
    }
  },
  computed: {
    ...mapState({
      curContractNo: state => state.curContractNo
    })
  },
  created () {
    this.getOutlayList()
  },
  methods: {
    // 返回
    back () {
      this.$router.push({name: 'InfoDynamicTable'})
    },
    getOutlayList () {
      var tmpData = '<?xml version="1.0" encoding="utf-8"?>'
      tmpData += '<soap:Envelope xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:xsd="http://www.w3.org/2001/XMLSchema"> '
      tmpData += '<soap:Body> '
      tmpData += '<JA_LIST xmlns="http://tempuri.org/">'
      tmpData += "<FSQL><![CDATA[select * from zz_fee where f11='" + this.curContractNo + "']]></FSQL>"
      tmpData += '</JA_LIST>'
      tmpData += '</soap:Body>'
      tmpData += '</soap:Envelope>'

      this.Http.post('JA_LIST', tmpData
      ).then(res => {
        let xml = res.data
        let parser = new DOMParser()
        let xmlDoc = parser.parseFromString(xml, 'text/xml')
        // 提取数据
        let Result = xmlDoc.getElementsByTagName('JA_LISTResponse')[0].getElementsByTagName('JA_LISTResult')[0]
        let HtmlStr = $(Result).html()
        let Info = (JSON.parse(HtmlStr))
        console.log(Info)
        this.outlayList = Info
      }).catch((error) => {
        console.log(error)
      })
    },
    // 导出
    exportExcel () {
      require.ensure([], () => {
        const { exportJsonToExcel } = require('../vendor/Export2Excel.js')
        const tHeader = ['单据编号', '申请日期', '事由', '申请人', '申请部门', '收款单位', '发生日期', '费用项目', '申请费用金额', '核算项目', '项目编号', '摘要', '备注']
        const filterVal = ['F1', 'F2', 'F3', 'F4', 'F5', 'F6', 'F7', 'F8', 'F9', 'F10', 'F11', 'F12', 'F13']
        const data = this.formatJson(filterVal, this.OutlayList)
        exportJsonToExcel(tHeader, data, '费用')
      })
    },
    formatJson (filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]))
    }
  }
}
</script>

<style lang="less" scoped>
</style>



